<template>
  <div>
    <v-row align="center" no-gutters style="height:150px">
      <v-col cols="11" no-gutters>
        <div class="text-h4 pl-10 blue--text text--darken-2" v-text="''"/>
      </v-col>
      <v-col cols="1">
        <v-btn
            color="primary"
            @click="loadPermissionSaveDialog"
            v-text="''"
        />
      </v-col>
    </v-row>
    <v-divider></v-divider>
    <el-table
        id="table"
        v-loading="table.loading"
        :data="table.data"
        height="68vh"
        highlight-current-row
        :expand-row-keys="['1']"
        :tree-props="{children: 'children'}"
        row-key="id">
      stripe
      style="width: 100%">
      <el-table-column
          :index="(index) => index + 1"
          align="center"
          label=""
          type="index"
          width="70px">
      </el-table-column>
      <el-table-column
          align="center"
          label="ID"
          property="id"
          width="120px">
      </el-table-column>
      <el-table-column
          label=""
          property="weight">
      </el-table-column>
      <el-table-column
          label=""
          property="permissionName">
      </el-table-column>
      <el-table-column
          label=""
          property="requestUrl">
      </el-table-column>
      <el-table-column
          label=""
          property="requestMethod">
      </el-table-column>
      <el-table-column align="center" label="" width="250px">
        <template v-slot="scope">
          <v-btn
              class="mx-1"
              color="blue darken-1"
              text
              @click="loadPermissionUpdateDialog(scope.row)"
              v-text="''"
          />
          <el-popconfirm
              icon="el-icon-info"
              icon-color="red"
              title=""
              @confirm="deletePermission(scope.row.id)"
          >
            <v-btn
                slot="reference"
                class="mx-1"
                color="red darken-1"
                text
                v-text="''"
            />
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <v-dialog
        v-model="dialog.isShow"
        max-width="600px"
    >
      <v-card>
        <v-card-title>
          <span class="text-h5" v-text="dialog.title"/>
        </v-card-title>
        <v-card-text class="pb-0">
          <v-form ref="permissionSaveOrUpdateForm">
            <v-container>
              <v-row no-gutters>
                <v-col class="pr-3" cols="6">
                  <v-text-field
                      v-model="dialog.permission.permissionName"
                      :counter="rules.permissionNameMaxLength"
                      :rules="[rules.isPermissionName]"
                      clearable
                      label=""
                  />
                </v-col>
                <v-col class="pr-3" cols="6">
                  <v-text-field
                      v-model="dialog.permission.parentId"
                      :rules="[(value) => !!value || 'ID', rules.isInteger]"
                      clearable
                      label="ID"
                  />
                </v-col>
                <v-col class="pr-3" cols="6">
                  <v-text-field
                      v-model="dialog.permission.weight"
                      :rules="[(value) => !!value || '', rules.isInteger]"
                      clearable
                      label=""
                  />
                </v-col>
                <v-col class="pr-3" cols="6">
                  <v-select
                      v-model="dialog.permission.requestMethod"
                      :items="enums.requestMethod"
                      label=""
                  />
                </v-col>
                <v-col class="pr-3" cols="12">
                  <v-text-field
                      v-model="dialog.permission.requestUrl"
                      :rules="[(value) => !!value || '']"
                      clearable
                      label=""
                  />
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
              color="grey darken-1"
              text
              @click="dialog.isShow = false"
              v-text="''"
          />
          <v-btn
              :disabled="dialog.btn.loading"
              :loading="dialog.btn.loading"
              color="blue darken-1"
              text
              @click="saveOrUpdatePermission"
              v-text="''"
          />
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "PermissionManager",
  components: {},
  computed: {
    isMobile: function () {
      return this.$vuetify.breakpoint.mobile;
    },
    isDark: function () {
      return this.$vuetify.theme.dark;
    }
  },
  watch: {},
  created() {
  },
  data: function () {
    return {
      table: {
        loading: false,
        data: null
      },
      dialog: {
        isShow: false,
        title: null,
        permission: {
          id: null,
          parentId: null,
          weight: null,
          permissionName: null,
          requestUrl: null,
          requestMethod: null,
          version: null
        },
        btn: {
          loading: false
        }
      },
      rules: this.GLOBAL.rules,
      enums: this.GLOBAL.enums
    };
  },
  methods: {
    changePageSize() {
      this.table.query.page.current = 1;
      this.pagePermission();
    },
    changePage() {
      this.pagePermission();
    },
    pagePermission() {
      this.table.loading = true;
      this.axios.get("/permission/listAllPermission")
          .then((response) => {
            this.table.data = response.data.data;
            this.table.loading = false;
          });
    },
    saveOrUpdatePermission() {
      if (!this.$refs.permissionSaveOrUpdateForm.validate()) {
        return;
      }
      this.dialog.btn.loading = true;
      if (this.dialog.permission.id) {
        this.axios.put("/permission/updatePermission", JSON.stringify(this.dialog.permission))
            .then(() => {
              this.$notify({
                title: "",
                message: null,
                type: "success",
                duration: 2000,
              });
              this.dialog.isShow = false;
              this.pagePermission();
            })
            .finally(() => {
              this.dialog.btn.loading = false;
            });
      } else {
        this.axios.post("/permission/savePermission", JSON.stringify(this.dialog.permission))
            .then(() => {
              this.$notify({
                title: "",
                message: null,
                type: "success",
                duration: 2000,
              });
              this.dialog.isShow = false;
              this.pagePermission();
            })
            .finally(() => {
              this.dialog.btn.loading = false;
            });
      }
    },
    deletePermission(permissionId) {
      this.axios.delete("/permission/removePermission/" + permissionId)
          .then(() => {
            this.showDialog = false;
            this.$notify({
              title: "",
              message: null,
              type: "success",
              duration: 2000,
            });
            this.pagePermission();
          });
    },
    loadPermissionSaveDialog() {
      this.dialog.permission.id = null;
      this.dialog.permission.parentId = null;
      this.dialog.permission.weight = null;
      this.dialog.permission.permissionName = null;
      this.dialog.permission.requestUrl = null;
      this.dialog.permission.requestMethod = null;
      this.dialog.permission.version = null;
      this.dialog.title = "";
      this.dialog.isShow = true;
    },
    loadPermissionUpdateDialog(permission) {
      this.dialog.permission.id = permission.id;
      this.dialog.permission.parentId = permission.parentId;
      this.dialog.permission.weight = permission.weight;
      this.dialog.permission.permissionName = permission.permissionName;
      this.dialog.permission.requestUrl = permission.requestUrl;
      this.dialog.permission.requestMethod = permission.requestMethod;
      this.dialog.permission.version = permission.version;
      this.dialog.title = "";
      this.dialog.isShow = true;
    },
  },
  mounted() {
    this.pagePermission();
  },
};
</script>

<style lang="scss">
#table {
  td, th.is-leaf {
    border-bottom: 0 solid #E0E0E0;
  }

  &:before {
    background-color: #E0E0E0;
  }
}

.el-popconfirm__main {
  margin-bottom: 10px;
}

.avatar-uploader {
  .el-upload {
    border: 1px solid #949494;
    border-radius: 6px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    width: 150px;
    height: 150px;

    &:hover {
      border-color: #409EFF;
    }
  }
}

</style>