  iframe: "http://127.0.0.1:8080",
  file: "http://47.95.124.239/static",
  upload: "http://47.95.124.239/api/file/upload"
};
const title = "";
const adminTitle = "";
const rules = {
  usernameMaxLength: 20,
  passwordMinLength: 8,
  passwordMaxLength: 30,
  roleNameMaxLength: 20,
  permissionNameMaxLength: 20,
  isEmail: (value) => !value || /.+@.+\..+/.test(value) || "",
  isInteger: (value) => !value || /^\d+$/.test(value) || "",
  isFloat: (value) => !value || /^\d+$/.test(value) || /^\d+[.]?\d+$/.test(value) || "",
  isUsername: (value) => {
    if (!!value && value.length <= 20) {
      return true;
    }
    if (!value) {
      return '';
    }
    if (value.length > 20) {
      return '20';
    }
  },
  isPassword: (value) => {
    if (!!value && value.length >= 8 && value.length <= 30) {
      return true;
    }
    if (!value) {
      return '';
    }
    if (value.length < 8) {
      return '8';
    }
    if (value.length > 30) {
      return '30';
    }
  },
  isPhoneNum: (value) => {
    if (!value) {
      return true;
    }
    if (/^1\d+$/.test(value) && value.length <= 11) {
      return true;
    }
    if (!/^1\d+$/.test(value)) {
      return ''
    }
    if (!value.length <= 11) {
      return '11';
    }
  },
  isRoleName: (value) => {
    if (!!value && value.length <= 20) {
      return true;
    }
    if (!value) {
      return '';
    }
    if (value.length > 20) {
      return '20';
    }
  },
  isPermissionName: (value) => {
    if (!!value && value.length <= 20) {
      return true;
    }
    if (!value) {
      return '';
    }
    if (value.length > 20) {
      return '20';
    }
  },
}
