<template>
  <div>
    <v-row style="height:150px" no-gutters align="center">
      <v-col cols="2" no-gutters>
        <div class="text-h4 pl-10 blue--text text--darken-2" v-text="''"/>
      </v-col>
      <v-col cols="9">
        <v-row no-gutters align="center">
          <v-col cols="5">
            <v-form style="width:100%" ref="userQueryForm">
              <v-row no-gutters align="center">
                <v-col cols="6">
                  <v-text-field
                      dense
                      clearable
                      class="mr-2"
                      v-model="table.query.user.id"
                      :rules="[rules.isInteger]"
                      label="ID"
                  >
                  </v-text-field>
                </v-col>
                <v-col cols="6">
                  <v-text-field
                      clearable
                      class="mr-2"
                      dense
                      v-model="table.query.user.username"
                      label=""
                  >
                  </v-text-field>
                </v-col>
              </v-row>
              <v-row no-gutters align="center">
                <v-col cols="6">
                  <v-select
                      class="mt-0 pt-0 mr-2"
                      v-model="table.query.user.status"
                      clearable
                      :items="enums.userStatus"
                      label=""
                  />
                </v-col>
                <v-col cols="6">
                  <v-select
                      v-model="table.query.user.roleIdList"
                      chips
                      clearable
                      class="mt-0 pt-0 mr-2"
                      deletable-chips
                      small-chips
                      :items="roleMap"
                      label=""
                      multiple
                  />
                </v-col>
              </v-row>
            </v-form>
          </v-col>
          <v-col cols="1">
            <v-btn
                small
                fab
                class="ml-2"
                color="primary"
                @click="pageUser"
            >
              <v-icon>
                mdi-magnify
              </v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="1">
        <v-btn
            color="primary"
            @click="loadUserSaveDialog"
            v-text="''"
        />
      </v-col>
    </v-row>
    <v-divider></v-divider>
    <el-table
        v-loading="table.loading"
        id="table"
        :data="table.data"
        stripe
        highlight-current-row
        height="68vh"
        style="width: 100%">
      <el-table-column
          label=""
          type="index"
          align="center"
          width="70px"
          :index="(index) => index + 1">
      </el-table-column>
      <el-table-column
          label="ID"
          property="id"
          align="center"
          sortable
          width="90px">
      </el-table-column>
      <el-table-column
          label=""
          property="username">
      </el-table-column>
      <el-table-column
          label=""
          property="email"
      >
      </el-table-column>
      <el-table-column
          label=""
          property="phoneNum"
          width="180">
      </el-table-column>
      <el-table-column label="" align="center">
        <template v-slot="scope">
          <v-avatar size="40" v-if="scope.row.avatarUrl!=null">
            <img :src="getAvatarPath(scope.row.avatarUrl)">
          </v-avatar>
          <v-icon size="40" v-else>mdi-account-circle</v-icon>
        </template>
      </el-table-column>
      <el-table-column
          label=""
          align="center"
          width="200">
        <template slot-scope="scope">
          <v-chip
              v-for="item in scope.row.roleIdList"
              :key="item"
              class="mx-1"
              label
              small
              v-text="roleNameFormatter(item)"
          >
          </v-chip>
        </template>
      </el-table-column>
      <el-table-column
          label=""
          :formatter="userStatusFormatter"
      >
      </el-table-column>
      <el-table-column label="" align="center" width="250px">
        <template v-slot="scope">
          <v-btn
              text
              color="blue darken-1"
              class="mx-1"
              @click="loadUserUpdateDialog(scope.row)"
              v-text="''"
          />
          <el-popconfirm
              icon="el-icon-info"
              icon-color="red"
              title=""
              @confirm="deleteUser(scope.row.id)"
          >
            <v-btn
                slot="reference"
                text
                color="red darken-1"
                class="mx-1"
                v-text="''"
            />
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <v-row class="mt-7" no-gutters>
      <v-col cols="9" class="d-flex justify-end">
        <div style="width:90px">
          <v-select
              :items="[5,15,50,100]"
              label=""
              lined
              dense
              v-model="table.query.page.size"
              @input="changePageSize"
          ></v-select>
        </div>
      </v-col>
      <v-col cols="3">
        <v-pagination
            prev-icon="mdi-menu-left"
            next-icon="mdi-menu-right"
            v-model="table.query.page.current"
            :length="table.query.page.count"
            @input="changePage"
            total-visible="5"
        ></v-pagination>
      </v-col>
    </v-row>
    <v-dialog
        v-model="dialog.isShow"
        max-width="600px"
    >
      <v-card>
        <v-card-title>
          <span class="text-h5" v-text="dialog.title"/>
        </v-card-title>
        <v-card-text class="pb-0">
          <v-form ref="userSaveOrUpdateForm">
            <v-container>
              <v-row no-gutters>
                <v-col cols="6" class="pr-3">
                  <v-text-field
                      v-model="dialog.user.username"
                      :disabled="!!dialog.user.id"
                      label=""
                      :counter="rules.usernameMaxLength"
                      :rules="[rules.isUsername]"
                      clearable
                  />
                </v-col>
                <v-col class="pl-3" cols="6">
                  <v-text-field
                      v-model="dialog.user.phoneNum"
                      :rules="[rules.isPhoneNum]"
                      counter="11"
                      :disabled="!!dialog.user.id"
                      label=""
                      clearable
                  />
                </v-col>
                <v-col cols="12">
                  <v-text-field
                      v-model="dialog.user.email"
                      :rules="[rules.isEmail]"
                      :disabled="!!dialog.user.id"
                      label=""
                      clearable
                  />
                </v-col>
                <v-col cols="12">
                  <v-text-field
                      v-if="!dialog.user.id"
                      v-model="dialog.user.password"
                      :rules="[rules.isPassword]"
                      type="password"
                      label=""
                      :counter="rules.passwordMaxLength"
                      clearable
                  />
                  <v-text-field
                      v-if="!dialog.user.id"
                      type="password"
                      label=""
                      :rules="[value=>value===dialog.user.password||'']"
                      :counter="rules.passwordMaxLength"
                      clearable
                  />
                </v-col>
                <v-col cols="6">
                  <v-row align="center" class="mb-3 " no-gutters>
                    <div :style="{color:dialog.user.id?'#9E9E9D':'inherit'}" class="text-subtitle-1" v-text="''"/>
                    <v-btn
                        v-if="dialog.user.avatarUrl!=null && !dialog.user.id"
                        x-small
                        depressed
                        class="error"
                        style="margin-left:72px"
                        @click="dialog.user.avatarUrl=null;"
                        v-text="''"
                    />
                  </v-row>
                  <el-upload
                      v-if="!dialog.user.id"
                      class="avatar-uploader"
                      name="uploadFile"
                      :action="upload.path"
                      :headers="upload.header"
                      :show-file-list="false"
                      :on-success="handleAvatarSuccess"
                      :on-error="handleAvatarError"
                      :before-upload="beforeAvatarUpload">
                    <img v-if="dialog.user.avatarUrl" :src="getAvatarPath(dialog.user.avatarUrl)"
                         style="height: 150px;width: 150px">
                    <v-icon v-else>mdi-upload</v-icon>
                  </el-upload>
                  <div v-else>
                    <img v-if="dialog.user.avatarUrl" :src="getAvatarPath(dialog.user.avatarUrl)"
                         style="height: 150px;width: 150px;border: 2px dashed #9E9E9D;border-radius: 6px;">
                    <v-icon v-else size="150px"
                            style="height: 150px;width: 150px;border: 2px dashed #9E9E9D;border-radius: 6px;">
                      mdi-account-circle
                    </v-icon>
                  </div>
                </v-col>
                <v-col cols=" 6" align-self="center">
                  <v-select
                      v-model="dialog.user.status"
                      class="pl-3"
                      :items="enums.userStatus"
                      label=""
                  />
                  <v-select
                      v-model="dialog.user.roleIdList"
                      class="pl-3"
                      chips
                      deletable-chips
                      small-chips
                      :items="roleMap"
                      label=""
                      multiple
                  />
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
              color="grey darken-1"
              text
              @click="dialog.isShow = false"
              v-text="''"
          />
          <v-btn
              color="blue darken-1"
              text
              :disabled="dialog.btn.loading"
              :loading="dialog.btn.loading"
              @click="saveOrUpdateUser"
              v-text="''"
          />
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "UserManagement",
  components: {},
  computed: {
    isMobile: function () {
      return this.$vuetify.breakpoint.mobile;
    },
    isDark: function () {
      return this.$vuetify.theme.dark;
    }
  },
  watch: {},
  created() {
  },
  data: function () {
    return {
      table: {
        loading: false,
        data: null,
        query: {
          page: {
            current: 1,
            size: 15,
            count: null
          },
          user: {
            id: null,
            username: null,
            status: null,
            roleIdList: null
          },
        }
      },
      dialog: {
        isShow: false,
        title: null,
        user: {
          id: null,
          username: null,
          phoneNum: null,
          email: null,
          avatarUrl: null,
          status: null,
          roleIdList: null,
          version: null,
        },
        btn: {
          loading: false
        }
      },
      upload: {
        path: this.GLOBAL.url.upload,
        header: {Authorization: this.$store.getters.getToken}
      },
      roleMap: [],
      rules: this.GLOBAL.rules,
      enums: this.GLOBAL.enums
    };
  },
  methods: {
    changePageSize() {
      this.table.query.page.current = 1;
      this.pageUser();
    },
    changePage() {
      this.pageUser();
    },
    pageUser() {
      if (this.$refs.userQueryForm.validate()) {
        this.table.loading = true;
        this.axios.post("/user/pageUser/" + this.table.query.page.current + "/" + this.table.query.page.size, JSON.stringify(this.table.query.user))
            .then((response) => {
              this.table.data = response.data.data;
              this.table.query.page.count = response.data.count;
              this.table.loading = false;
            });
      }
    },
    saveOrUpdateUser() {
      if (!this.$refs.userSaveOrUpdateForm.validate()) {
        return;
      }
      this.dialog.btn.loading = true;
      if (this.dialog.user.id) {
        this.axios.put("/user/updateUser", JSON.stringify(this.dialog.user))
            .then(() => {
              this.$notify({
                title: "",
                message: null,
                type: "success",
                duration: 2000,
              });
            })
            .finally(() => {
              this.dialog.btn.loading = false;
              this.dialog.isShow = false;
              this.pageUser();
            });
      } else {
        this.axios.post("/user/saveUser", JSON.stringify(this.dialog.user))
            .then(() => {
              this.$notify({
                title: "",
                message: null,
                type: "success",
                duration: 2000,
              });
            })
            .finally(() => {
              this.dialog.btn.loading = false;
              this.dialog.isShow = false;
              this.pageUser();
            });
      }
    },
    deleteUser(userId) {
      this.axios.delete("/user/removeUser/" + userId)
          .then(() => {
            this.showDialog = false;
            this.$notify({
              title: "",
              message: null,
              type: "success",
              duration: 2000,
            });
            this.pageUser();
          });
    },
    loadUserSaveDialog() {
      this.dialog.user.id = null;
      this.dialog.user.username = null;
      this.dialog.user.phoneNum = null;
      this.dialog.user.email = null;
      this.dialog.user.avatarUrl = null;
      this.dialog.user.status = null;
      this.dialog.user.roleIdList = null;
      this.dialog.user.version = null;
      this.dialog.title = "";
      this.dialog.isShow = true;
    },
    loadUserUpdateDialog(user) {
      this.dialog.user.id = user.id;
      this.dialog.user.username = user.username;
      this.dialog.user.phoneNum = user.phoneNum;
      this.dialog.user.email = user.email;
      this.dialog.user.avatarUrl = user.avatarUrl;
      this.dialog.user.status = user.status;
      this.dialog.user.roleIdList = user.roleIdList;
      this.dialog.user.version = user.version;
      this.dialog.title = "";
      this.dialog.isShow = true;
    },
    getAvatarPath(avatarUrl) {
      if (avatarUrl != null) {
        return this.GLOBAL.url.file + "/" + avatarUrl;
      }
    },
    handleAvatarSuccess(res) {
      this.dialog.user.avatarUrl = res.data;
      this.$notify({
        title: "",
        message: null,
        type: "success",
        duration: 2000,
      });
    },
    handleAvatarError(err) {
      console.log(err)
      this.$message.error("")
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error(' JPG !');
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }
      return isJPG && isLt2M;
    },
    roleNameFormatter(roleId) {
      for (let item of this.roleMap) {
        if (item.value === roleId) {
          return item.text;
        }
      }
    },
    userStatusFormatter(row) {
      console.log(row)
      for (let item of this.enums.userStatus) {
        if (item.value === row.status) {
          return item.text;
        }
      }
    },
    loadRoleMap() {
      let _this = this;
      this.axios.get("/role/listAllRole").then((response) => {
        response.data.data.forEach(item => {
          let role = {
            text: item.roleName,
            value: item.id
          }
          _this.roleMap.push(role)
        })
      });
    }
  },
  mounted() {
    this.pageUser();
    this.loadRoleMap();
  },
};
</script>

<style lang="scss">
#table {
  td, th.is-leaf {
    border-bottom: 0 solid #E0E0E0;
  }

  &:before {
    background-color: #E0E0E0;
  }
}

.el-popconfirm__main {
  margin-bottom: 10px;
}

.avatar-uploader {
  .el-upload {
    border: 1px solid #949494;
    border-radius: 6px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    width: 150px;
    height: 150px;

    &:hover {
      border-color: #409EFF;
    }
  }
}

</style>